body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.loadedItems>ul{
	list-style: none;
    padding: 0px;
    margin-bottom: 10px;
    float: left;
    margin-top: 10px;
}
.no-padding-lr{
	padding-left:0px !important;
	padding-right:0px !important;
}
.loadedItems>ul>li{
	    border: 1px solid #d8d8d8;
    padding: 15px 0px;
    width: 100%;
    float: left;
	height: 120px;
    box-shadow: 1px 4px 7px #dedede;
}.loadedItems>ul>li span{
	vertical-align: top;
    /* margin-left: 41px; */
    font-size: 16px;
    display: block;
}.loadedItems>ul>li span.manufacture{
	font-weight: 500;
    font-size: 10px;
    margin-top: 7px;
    color: #0400ea;
}.loadedItems>ul>li span.manufacture.green{
    color: #0da74a;
}.loadedItems>ul div{
	float:left;
}
.loadedItems>ul>li img{
	width: 100px;
	    border: 1px solid gray;
}
.headings >p{
	margin-bottom: 13px;
    margin-top: 12px;
    font-size: 20px;
    font-weight: bold;
    color: #b10564;
}
.inputsNew button {
    display: inline-block;
    background: transparent;
    padding: 0px;
    box-shadow: none;
    border: none;
    margin: 3px 10px;
    color: #7d7d8e;
    font-size: 10px;
	outline: none !important;
}
.totalCounts{
	    font-size: 15px;
    position: absolute;
    right: 19px;
    top: 16px;
    color: black;
}
.priceList .price{
    margin-bottom: 7px;
}.priceList  .discountedPrice input{
	width: 30px;
    height: 21px;
    text-align: center;
    margin-left: 4px;

}
.pull-left{
	float: left;
}.priceList  .discountedPrice{
        color: #c50606;
    font-weight: bold;
}
.cartBackground{
	background: #f6f7f9;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 10px;
}
.originalPrice{
	    display: inline-block !important;
    margin-left: 10px;
    text-decoration: line-through;
}
.inputsNew span {
	display: inline-block !important;
}
.inputsNew input {
        width: 37px;
    padding: 0px 5px;
    border: 1px solid #e0e0e0;
    margin-top: 2px;
    background: #efe9e9;
    text-align: center;
}
.newOptions ul{
	list-style: none;
	padding-left: 0px;
}
.newOptions >ul>li {
    float: left;
    margin-left: 10px;
	cursor: default;
}
.newOptions >ul>li>span {
    color: #1c861c;
}
.cartlist{
	    list-style: none;
    padding-left: 7px;
    font-weight: bold;
}.cartlist>li>span{
	        float: right;
    font-size: 12px;
    font-weight: 400;
    color:#0400ea;
}.cartlist>li>span>span{
	      margin-left: 5px;
    color: black;
}
.cartlist>li {
    margin-bottom: 10px;
}
.cartheader{
	border-bottom: 1px solid #ccc9c9;
    padding-bottom: 7px;
}
.fontSize12{
	font-size: 12px;
}
.cartlist .priceList{
	margin-top: 10px;
}
.cartlist>li {
    margin-bottom: 10px;
    border-bottom: 1px solid #cacaca;
}
.cartlist>li:last-child{
	border-bottom: none !important;
}
.finalAmt{
	float:right;
}
.modalclose{
	    position: absolute;
    right: 1px;
    top: 1px;
}
fieldset label {
    display: block;
}
fieldset input {
    margin-right: 10px;
}
.pull-right{
	float:right;
}
.cartMobile{
		display:none !important;
	}
@media(max-width: 1200px){
	.loadedItems>ul>li{
		height: 100% !important;
	}
	.loadedItems>ul>li img{
		width: 80px !important;
	}
}@media(max-width: 500px){
	.removeright{
		margin: 0px 10px !important;
	}
	.inputsNew{
		width:50px !important;
		    margin-bottom: 10px;
	}
}@media(max-width: 700px){
	.ReactModal__Content{
    top: 0px !important;
    bottom: 0px !important;
    left: 0px !important;
    right: 0px !important;
}
	.newOptions >ul>li{
		float:right !important;
	}
	.inputsNew{
		float:right !important;
		padding: 0px !important;
		text-align: right;
	}
	.removeleft{
		padding-left: 0px !important;
		min-height: 80px;
	}
	.removeright{
		margin-right: 0px;
	}
	.cartMobile{
		    display: block !important;
    position: fixed;
    bottom: 21px;
    right: 17px;
    z-index: 0;
    cursor: pointer;
	}
	.cartMobile img {
    width: 72px;
    background: #f6f7f9;
    padding: 10px 20px;
    border: 1px solid #cacaca;
    bottom: 0px;
}.cartMobile span.no{
    position: absolute;
    color: white;
    z-index: 1000;
    right: 7px;
    border: 1px solid red;
    padding: 2px 4px;
    background: red;
    font-size: 10px;
}
	.margin-bottom-15{
	    margin-bottom: 15px !important;
}
}
.margin-bottom-15{
	    margin-bottom: 5px;
}.margin-bottom-10{
	    margin-bottom: 10px;
}
.deletebtn{
	    float: right;
    font-size: 11px;
    color: #dc3545 !important;
    display: inline-block;
    cursor: pointer;
    margin-top: 1px;
}